import React from 'react';
import '../App.css';
import {Grid,Row,Col,Glyphicon} from 'react-bootstrap';

function Data(props){
    return (
        <div>
             <Col md={4}><span className={props.img} style={{fontSize: "50px",margin: "15px 0px 0px 50px"}} ></span> </Col>
            <Col md={6}>hello, {props.data}</Col>
            
        </div>
    )
    
}

class Information extends React.Component{
    render(){
        return(
            <div className="info">
              <Row className="up custom_row">
                <Col md={6} xs={12}>
                     <Data data="There are many variations of passages of Lorem Ipsum available, but the majority have suffered alteration in some form, by in" img="glyphicon glyphicon-search" />
                </Col>
                <Col md={6} xs={12}>
                     <Data data="There are many variations of passages of Lorem Ipsum available, but the majority have suffered alteration in some form, by in" img="glyphicon glyphicon-plane" />
                </Col>
             </Row>
             <Row className="up custom_row">
                <Col md={6} xs={12}>
                    <Data data="There are many variations of passages of Lorem Ipsum available, but the majority have suffered alteration in some form, by in" img="glyphicon glyphicon-headphones" />
                </Col>
                <Col md={6} xs={12}>
                    <Data data="There are many variations of passages of Lorem Ipsum available, but the majority have suffered alteration in some form, by in" img="glyphicon glyphicon-camera" /> 
                </Col>
             </Row>
            </div>
        )
    }
}


export default Information;


