import React from 'react';
import ReactDOM from 'react-dom';
import {BrowserRouter,Route,NavLink,Link} from 'react-router-dom';
import './index.css';
import Practiceapp from './practice/Practiceapp';
import Form from './practice/Form';
import Products from './practice/Products';
import App from './App';
import registerServiceWorker from './registerServiceWorker';

// const PagenotFound=()=>(
//     <div>
//     404 -File not Found <Link to="/">Go Back To Home Page</Link>
// </div>
// )

const routes=(
            <BrowserRouter>
               <div>

               <Route path="/" component={Practiceapp} exact={true} />
               <Route path="/form" component={Form}  />
               <Route path="/products" component={Products} />
               {/* <Route path="/products/:id" component={Products} /> */}
               {/* <Route component={PagenotFound} /> */}

               </div>

            </BrowserRouter>
);

ReactDOM.render(routes, document.getElementById('root'));
registerServiceWorker();



