import React from 'react';
import MYNavigationbar from './Navbar';
import Footer from './Footer';
import {Row,Col} from 'react-bootstrap';

class Form extends React.Component{
    constructor(props){
        super(props)

        this.hideSignup=this.hideSignup.bind(this);
    }


hideSignup() {
    document.getElementById("signup").style.display='none';
    document.getElementById("login").style.display='block';
    document.getElementById("loginbtn").style.backgroundColor = "#2ebcbc";
    document.getElementById("signupbtn").style.backgroundColor = "#3d4153";
}

hidelogin() {
    document.getElementById("signup").style.display='block';
    document.getElementById("login").style.display='none';
    document.getElementById("signupbtn").style.backgroundColor = "#2ebcbc";
    document.getElementById("loginbtn").style.backgroundColor = "#3d4153";
}

    render(){
        return(
          <Row className="custom_row" style={{backgroundColor:"#2a283d",color:"wheat"}}>
                <MYNavigationbar />
           <Row className="custom_row form">
                <Row className="">
                    <Col md={6} className="text-center signup" id="signupbtn" onClick={this.hidelogin}>
                         Sign Up
                    </Col>
                    <Col md={6} className="text-center login" id="loginbtn" onClick={this.hideSignup}>
                        Log In
                    </Col> 
                </Row>
                <div id="signup">
                <h3 className="text-center"> Sign Up For Free</h3>
                <Row className="up2">
                    <Col md={6} className="">
                        <input type="text" value="" placeholder="first name" />
                    </Col>
                    <Col md={6} className="">
                    <input type="text" value="" placeholder="last name" />
                    </Col> 
                </Row>
                <Row className="up2">
                    <Col md={12} className="">
                        <input type="text" value="" placeholder="Email adress" />
                    </Col>
                </Row>
                <Row className="up2">
                    <Col md={12} className="">
                        <input type="text" value="" placeholder="Set password" />
                    </Col>
                </Row>
                </div>

                <div id="login" style={{display:"none"}}>
                    <h3 className="text-center"> Login </h3>
                    <Row className="up2">
                    <Col md={12} className="">
                        <input type="text" value="" placeholder="Username" />
                    </Col>
                    </Row>
                    <Row className="up2">
                        <Col md={12} className="">
                            <input type="text" value="" placeholder="Password" />
                        </Col>
                    </Row>
                </div>
                <Row className="text-center getstart up2"> Get Started </Row>
            </Row>
            <Footer />
        </Row>
        )
    }
}

export default Form;


