import React from 'react';
import '../App.css';
import {BrowserRouter,Route,NavLink} from 'react-router-dom';
import { Navbar, Nav, NavItem, MenuItem, NavDropdown,Row } from 'react-bootstrap';
class MYNavigationbar extends React.Component {
    render() {
        return (
            <Row className="custom_row">
                <Navbar inverse collapseOnSelect style={{marginBottom:"0px"}}>
                    <Navbar.Header>
                        <Navbar.Brand>
                            <NavLink to="/" activeClassName="selected" exact={true}>React-Bootstrap</NavLink>
                           
                        </Navbar.Brand>
                        <Navbar.Toggle />
                    </Navbar.Header>
                    <Navbar.Collapse>
                        <Nav>
                            <NavItem eventKey={1} href="">
                            <NavLink to="form" activeClassName="selected">form</NavLink>
                             </NavItem>
                            <NavItem eventKey={2} href="products">
                            <NavLink to="products" activeClassName="selected">products</NavLink>
                              </NavItem>
                            <NavDropdown eventKey={3} title="Dropdown" id="basic-nav-dropdown">
                                <MenuItem eventKey={3.1}>Action</MenuItem>
                                <MenuItem eventKey={3.2}>Another action</MenuItem>
                                <MenuItem eventKey={3.3}>Something else here</MenuItem>
                                <MenuItem divider />
                                <MenuItem eventKey={3.3}>Separated link</MenuItem>
                            </NavDropdown>
                        </Nav>
                        <Nav pullRight>
                            <NavItem eventKey={1} href="#">
                                link
                              </NavItem>
                            <NavItem eventKey={2} href="#">
                                Link Right
                             </NavItem>
                        </Nav>
                    </Navbar.Collapse>
                </Navbar>
             </Row>
        )
    }
}


export default MYNavigationbar;


