import React from 'react';
import {Row,Col} from 'react-bootstrap';
import { SocialIcon } from 'react-social-icons';

class Footer extends React.Component{
    render(){
        return(
            <Row className="custom_row footer">
                <Col md={4} xs={12}>
                <div className="icondiv">
                <span className="iconspan"><SocialIcon className="twitter-social-icon" url="http://twitter.com/" color="currentColor"  /></span>
                <span className="iconspan"><SocialIcon  className="linkdin-social-icon" url="https://www.linkedin.com/" color="currentColor" /></span>
                <span className="iconspan"><SocialIcon className="github-social-icon" url="http://www.github.com/" color="currentColor" /></span>
                <span className="iconspan"><SocialIcon className="insta-social-icon" url="https://www.instagram.com/" color="currentColor" /></span>
                </div>
                </Col>
                <Col md={4} xs={12}>
                <p className="footerline">© 2018 ReactPractices, Inc@.</p>
                </Col>
                <Col md={3} xs={12}>
                   <p className="footertext">There are many variations of passages of Lorem Ipsum available, but the majority have suffered alteration in som</p>
                </Col>
            </Row>
           
        )
    }
}

export default Footer;


